function Home() {
    // const classes = useStyles();
    const history = useHistory()
  

    const [user, setUser] = useState('')

    // fetch token from localstorage
    const getUser = async()=>{
        const res = await axios.get('https://cors-anywhere.herokuapp.com/http://13.234.118.47/api/profile/',{
            headers:{
                "Content-Type":"application/x-www-form-urlencoded",
                Authorization : `token 69c5b4bf7f0b925a754bd7dd730d70a7e641fa4d66431231b187c011582fd420`,
            }
        })
        setUser(res.data.data)
    }
    useEffect(()=>{
        getUser()
    },[])
    console.log(user)

    const handleSignOut = (e)=>{
      e.preventDefault()
      localStorage.removeItem('token')
      history.push('/')
    }
    return (
        <div>
          <Button onClick={handleSignOut} className="logout__btn" variant="contained" color="primary">
        Log out
      </Button>
            <Card className="card">
      <CardContent >
      
        <Typography className="data" variant="h5" component="h2">
        {user.username}
        </Typography>

        <Typography className="data" variant="h5" component="h2">
        {user.email}
        </Typography>

        <Typography className="data" variant="h5" component="h2">
        {user.phone_number}
        </Typography>

        <Typography className="data" variant="h5" component="h2">
        {user.designation}
        </Typography>
        
      </CardContent>
      
    </Card>
        </div>
    )
}

