import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom"
import './App.css';
import SignIn from './SignIn'
import Home from './Home'

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
        <Route path='/home'>
        <Home />
        </Route>
        <Route path="/">
        <SignIn />
        </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;


